import loadErr from '@/components/loadErr.vue'
import { mapGetters , mapActions } from 'vuex';

export default {
	data() {
		return {
			addressList: [],
			isLoadErr: false, //页面加载出错
			fromType: null ,//页面来处
		}
	},
	onLoad(opt) {
		this.fromType = opt.from
	},
	onShow() {
		this.getAddressData()
	},
	methods: {
		...mapActions([
			'toGetAddressObj'
		]),
		toOptAddress(type, idx) {
			if(type == 'edit') {
				this.toGetAddressObj(this.addressList[idx])
			}
			uni.navigateTo({
				url: `/pages/optAddress/optAddress?type=${type}`
			})
		},
		selectGetAddress(idx) { //选择收货地址
			if(this.fromType != 'mine') {
				uni.navigateBack({
					delta: 1
				})
			}
		},
		toDeleteAddress(idx) {
			uni.showModal({
				title: '',
				content: '确认删除地址?',
				cancelColor: '#909399',
				confirmColor: '#FFDA4C',
				success: (res) => {
					if(res.confirm) {
						this.deleteAddressMay(idx)
					}else {
						this.$api.warnNotice('已取消操作')
					}
				},
				fail: () => {
					this.$api.warnNotice('操作失败')
				}
			})
		},
		deleteAddressMay(idx) {
			const $this = this
			this.$api.ajax({
				url: this.$config.deleteAddress,
				method: 'DELETE',
				data: {
					adr_id: this.addressList[idx].adr_id,
				}
			})
			.then((res) => {
				if(res.statusCode == 200 && res.data.code == 0) {
					uni.showToast({
						title: '操作成功',
						icon: 'success',
						success: () => {
							$this.addressList.splice(idx, 1)
						}
					})
				}else {
					this.$api.warnNotice(res.data.message)
				}
			})
			.catch((err) => {
				this.$api.warnNotice('操作失败')
			})
		},
		toRelaodData() {
			this.isLoadErr = false
			this.addressList = []
			this.getAddressData()
		},
		getAddressData() {
			uni.showLoading({
				title: '数据加载中',
				mask: true
			})
			this.$api.ajax({
				url: this.$config.addressList,
			})
			.then((res) => {
				if(res.statusCode == 200 && res.data.code == 0) {
					let arr = res.data.data
					
					this.addressList = arr

					setTimeout(() => {
						uni.hideLoading();
					}, 500)
				}else {
					if(this.index == 1) {
						this.isLoadErr = true
					}else {
						this.$api.warnNotice(res.data.message)
					}
					uni.hideLoading();
				}
			})
			.catch((err) => {
				if(this.index == 1) {
					this.isLoadErr = true
				}else {
					this.$api.warnNotice('列表加载失败')
				}
				uni.hideLoading();
			})
		}
	}
}
